.sidebar {
  min-width: 230px;
  max-width: 230px;
  width: 200px;
  background-color: #ffffff;
  margin-left: -230px;
  padding: 20px;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  transition: all 0.5s;
  min-height: 100vh !important;
}
