.user-profile {
  display: flex;
  align-items: center;
  /* padding-right: 1px;
  bottom: 1px; */
  border: 1px solid #8AC007;
}

.user-profile p {
  /* margin: 0; */
  font-size: 10px;
  font-weight: bold;
  color: #333;
  line-height: 1.4; /* Improved readability */
}

.image-container{
 margin: 3px;
}
.sidebar.is-open {
  margin-left: 0px;
  transition: 0.5s;
  min-height: 100vh !important;
}

.sidebar-header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1;
  /* Ensure the header stays on top when scrolling */
  background: white;
  color: #000509;
}

.sidebar-header h3 {
  color: #fff;
}

.sidebar ul p {
  color: #fff;
  padding: 10px;
}

.collapse.show,
.collapsing {
  background: #f7f7f9;
}

.breadcrumb {
  display: flex;
  flex-wrap: wrap;
  padding: .75rem 2rem;
  margin-left: .4rem;
  margin-right: .4rem;
  margin-bottom: 1rem;
  list-style: none;
  background-color: #fefefe;
  border-radius: .25rem;
}

.nav-item {
  /*color: #ced4da;*/
  color: black;
  font-weight: 200;
  
}

li .nav-item {
  margin-bottom: 5px;
}

.nav-item.open {
  color: white;
  background: rgb(219, 241, 219);
}


/* .nav-item .nav-link .active {
  background: #2d3646 !important;
  background: green;
  color: white;
} */

.nav-item .nav-link a.active {
  background-color: #0b572c;
  color: white;
  border-radius: 5px;
}



.nav-link {
  display: block;
  padding: 10px;
  border-radius: 5px;
}

.nav-item .active {
  /* background: #2d3646 !important; */
  background: green;
  color: white;
}

.nav-item:hover {
  background-color: #C0D8DE;
  color: rgb(77, 57, 57);
  border-radius: 5px;
  transition: 0.2s;
}

/* .nav-item:hover {
  background: #313b4c;
  background: lightgreen;
  color: white;
  transition: 0.2s;
} */

.nav-item .svg-inline--fa {
  color: inherit;
}

li a.dropdown-toggle::after {
  display: inline-flex;
  position: relative;
  left: 60%;
  top: 10%;
}

.sidebar-header>button {
  position: relative;
  float: right;
  margin: 0.5em;
  font-size: 2rem;
  cursor: pointer;
  display: none;
}

/* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */
.content {
  padding: 0px;
  margin-left: 0;
  height: auto;
}

.alert {
  position: relative;
  padding: 0.2rem 1rem;
  margin-bottom: .2rem;
  border: 1px solid transparent;
  border-radius: 0.25rem;
}

.card-form-title-dark {
  width: 100%;
  /* background-color: #001aff; */
  color: rgb(0, 0, 0);
  padding: 5px;
  border-radius: 3px;
}

.card-form-title-light {
  width: 100%;
  background-color: #bfeabd;
  color: black;
  padding: 5px;
  border-radius: 3px;
}

.alert-danger {
  color: #721c24;
  background-color: white !important;
  border-color: white !important;
}

@media only screen and (max-width: 575.98px) {
  /* body {
    overflow: hidden;
  } */

  .content.is-open {
    /*margin-left: 100%;*/
    display: none;
    transition: all 0.5s;
  }

  .sidebar.is-open {
    min-width: 100%;
    max-width: 100%;
    margin-left: 0;
    transition: all 0.5s, height 0s;
    height: 100vh !important;
  }

  .sidebar.is-open>.sidebar-header button {
    display: block;
  }

  li a.dropdown-toggle::after {
    display: inline-block;
    position: relative;
    left: 68%;
  }
}

.custom-dropdown-toggle .dropdown-toggle::after {
  display: none !important;
}
.custom-btn-group,
.btn-group-vertical {
  position: relative;
  display: inline-flex;
  vertical-align: middle !important;
  height: 48px;
  margin-top: 10px;
}
